@import "./src/scss/utils/variables";
@import "./src/scss/utils/mixins";

.page {
	@include main-content();

	.description {
		@include description();
		font-size: 18px;
	}

	.additional-material {
		text-align: left;
		margin-bottom: 40px;

		&__label {
		    margin-bottom: 20px;
		}

		&__list {
			padding-left: 20px;
		}

		&__item {
			margin-top: 7px;
		}
	}
}
